body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(40, 41, 47);
  color: rgb(196, 216, 233);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  border: 1px solid rgb(172, 182, 255);
  border-radius: 5%;
  background-color: rgb(187, 193, 211);
  margin-left: 5px;
}
#addButton {
  margin-bottom: 30px;
  margin-top: 10px;  
  margin-left: -65px;
}
#submitForm {
  margin-top: 40px;   
}
label {
  margin-right: 10px;
  display: inline-block;
  width: 40px;
  text-align: right;
}
input {
  display: inline-block;
  width:180px;
}
span {
  display: inline-block;
  width:180px;
  text-align: left;
}
#movieButtons {
  margin-top: 5px;
  margin-bottom: 10px;
  margin-left: 25px;
}
.movieDiv {
  background-color: rgb(60, 64, 69);
  width: 350px;
  display: inline-block;
}
.movieDiv label{
  margin-left: -100px;
}
.movieDiv input{
  width: 270px;
  margin-right: -100px;
}




